
import { getCurrentCity, getmanchanage, searchchanage } from '../API/manjsop.js'
const mixin = {
  data() {
    return {
      rowSelection,
      datalist: [],
      columns,
      pagination: {
        showQuickJumper: true,
        defaultPageSize: 10,
        showTotal: (total) => `共 ${total} 条数据`,
        showSizeChanger: true,
        defaultCurrent: 1,
        pageSizeOptions: ["10", "15", "20"],
        onShowSizeChange: (current, pageSize) => (this.pageSize = pageSize),
      },
    }

  },
  methods: {
    async managelist() {
      let data = await getCurrentCity()
      console.log(data);
      if (data.code == "0000") {
        let tabledata = data.data
      
        this.datalist = data.data
        this.strainer()
      }

    },
    async manchange() {
      let data = await getmanchanage()
      if (data.code == "0000") {
        this.datalist = data.data
        this.strainer()
      }
    },
    strainer() {
      this.datalist.map(item => {
        switch (item.order_status) {
          case 10:
            item.order_status = "待付款";
            break;
          case 27:
            item.order_status = "待发货";
            break;
          case 30:
            item.order_status = "待收货";
            break;
          case 20:
            item.order_status = "待审核"
            break;
          case 40:
            item.order_status = "已收货";
            break;
          case 50:
            item.order_status = "已完成";
            break;
          case 0:
            item.order_status = "售后中";
            break;
          case 21:
            item.order_status = "售后中";
            break;
          case 22:
            item.order_status = "售后中";
            break;
          case -1:
            item.order_status = "退款审核";
            break;
          case -2:
            item.order_status = "退款完成";
            break;

        }
      })
    },
    onDeta(item) {
      this.$router.push({
        path: "/notyet",
        query: {
          item: JSON.stringify(item),
        },
      });
    },
    onGoods(id) {
      this.$router.push({
        path: "/shipments",
        query: {
          id: JSON.stringify(id),
        },
      });
    },
  },
}
export default mixin
const columns = [
  {
    title: "订单号",
    dataIndex: "order_id",
  },
  {
    title: "商品名称",
    dataIndex: "img",
    scopedSlots: { customRender: "img" },
  },
  {
    title: "数量",
    dataIndex: "productListVo[0].count",
  },
  {
    title: "下单时间",
    dataIndex: "addTime",
  },
  {
    title: "状态",
    dataIndex: "order_status",
  },
  {
    title: "操作",
    dataIndex: "action",
    scopedSlots: { customRender: "action" },
  },
];


const rowSelection = {
  onChange: (selectedRowKeys, selectedRows) => {
    console.log(
      `selectedRowKeys: ${selectedRowKeys}`,
      "selectedRows: ",
      selectedRows
    );
  },
  onSelect: (record, selected, selectedRows) => {
    console.log(record, selected, selectedRows);
  },
  onSelectAll: (selected, selectedRows, changeRows) => {
    console.log(selected, selectedRows, changeRows);
  },
};





