import jsonp from './jsonp.js'

export function getCurrentCity() {  //订单列表
    // 接口地址
    let url = 'http://192.168.0.102:9001/orderForm/orderList'
    // 所需参数
    let data = {
        mer_Id: 1,
        currPage: 1,
        number: 1,
        mark: 1
    }
    return jsonp(url, data, {})
}
export function getmanchanage() {
    // 接口地址
    let url = 'http://192.168.0.102:9001/orderForm/orderList'
    // 所需参数
    let data = {
        mer_Id: 1,
        currPage: 1,
        number: sessionStorage.getItem('manage'),
        mark: sessionStorage.getItem('manage')
    }
    return jsonp(url, data, {})
}

export function delivery(id) { //发货详情
    // 接口地址
    let url = 'http://192.168.0.102:9001/orderForm/shippingDetail'
    // 所需参数
    let data = {
        id: id,
    }
    return jsonp(url, data, {})
}
export function shipmentslist(id) { //订单详情
    // 接口地址
    let url = 'http://192.168.0.102:9001/orderForm/orderDetails'
    // 所需参数
    let data = {
        id: id
    }
    return jsonp(url, data, {})
}


