<template>
  <!-- 订单列表 -->
  <div class="management-body">
    <div class="management-header">
      <a-breadcrumb separator=">">
        <a-breadcrumb-item>订单管理</a-breadcrumb-item>
        <a-breadcrumb-item>{{ this.$route.meta.title }}</a-breadcrumb-item>
      </a-breadcrumb>
      <hr />
      <div class="header-content">
        <div class="header-left">
          <div>
            订单号：<input
              type="text"
              v-model="searcgitem"
              placeholder="请输入订单号"
            />
          </div>
          <div>
            下单时间：
            <a-range-picker @change="onChange" disabled>
              <a-icon slot="suffixIcon" type="carry-out"
            /></a-range-picker>
          </div>
        </div>
        <div class="header-right">
          <div>
            订单状态：
            <select ref="item" style="pointer-events: none;">
              <option value="1">全部</option>
              <option value="2">未发货</option>
              <option value="3">已发货</option>
              <option value="4">已签收</option>
              <option value="5">售后中</option>
            </select>
          </div>
          <div>
            <button @click="search">搜索</button>
            <button @click="reset">重置</button>
            <button>表格导出</button>
          </div>
        </div>
      </div>
    </div>

    <div class="management-center">
      <div
        v-for="(item, index) in manlist"
        :key="index"
        :class="{ actives: isActive == index }"
        @click="change(index)"
      >
        {{ item.title }}
      </div>
    </div>
    <div class="management-buttom">
      <a-table
        :columns="columns"
        :data-source="datalist"
        :pagination="pagination"
        :row-selection="rowSelection"
        bordered
        rowKey="productListVo[0].goodsCartId"
      >
        <span slot="img" slot-scope="text, record">
          <img
            width="100px"
            :src="record.productListVo[0].goodsMainPhoto"
            alt=""
          />
          {{ record.productListVo[0].goodsName }}
        </span>
        <span slot="action" slot-scope="text, record">
          <a @click="onDeta(record)">查看详情</a>
          &nbsp;
          <a
            @click="onGoods(record.id)"
            v-show="record.order_status == '待发货'"
            >发货</a
          >
        </span>
      </a-table>
    </div>
  </div>
</template>

<script>
import manage from "@/utils/manage.js";
export default {
  mixins: [manage],
  data() {
    return {
      manlist: [
        { title: "" },
        { title: "全部订单" },
        { title: "待发货" },
        { title: "已发货" },
        { title: "已完成" },
        { title: "售后中" },
      ],
      isActive: 1,
      searcgitem: "",
      time: [],
    };
  },
  mounted() {
    this.managelist();
  },
  methods: {
    onChange(date, dateString) {
      this.time = dateString;
    },
    change(index) {
      this.isActive = index;
      sessionStorage.setItem("manage", index);
      this.manchange();
    },

    search() {
      let list = this.datalist.filter(
        (item) => item.order_id.indexOf(this.searcgitem) >= 0
      );
      this.datalist = list;
      this.searcgitem = "";
    },
    reset() {
      this.isActive = 1;
      this.managelist();
    },
  },
};
</script>

<style scoped>
.management-center a {
  text-decoration: none;
  color: #333;
}
.header-content {
  background: #d7d7d7;
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.header-left input {
  outline: none;
  width: 250px;
  height: 30px;
  border: 1px solid #c0c0c0;
}
.header-left {
  width: 50%;
  padding: 10px 10px;
}

.header-left > :nth-child(1) {
  width: 100%;
  margin-bottom: 10px;
  padding-left: 30px;
}
.header-left > :nth-child(2) {
  padding-left: 12px;
}
.header-left > :nth-child(2) > :nth-child(1) {
  width: 250px;
}
.header-right {
  width: 50%;
  padding: 10px 10px;
}
select {
  width: 300px;
  height: 30px;
  outline: none;
}
.header-right > :nth-child(1) {
  padding: 10px 10px;
}
.header-right > :nth-child(2) {
  padding: 0px 10px;
}
.header-right > :nth-child(2) button {
  width: 75px;
  height: 35px;
  margin-right: 20px;
}
.management-center {
  display: flex;
  justify-content: left;
  align-items: center;
  padding-top: 10px;
}
.actives {
  border-color: transparent transparent #c0c0c0 !important;
}
.management-center div {
  border-bottom: 36px solid #fff;
  border-left: 36px solid transparent;
  border-right: 0px solid transparent;
  height: 36px;
  line-height: 36px;
  width: 100px;
  text-align: center;
}
.management-center :nth-child(1) {
  display: none;
}
.management-center div:hover {
  cursor: pointer;
}
</style>